.container {
  display: flex;
  .builder {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding: 20px;

    gap: 20px;

    .addBtns {
      display: flex;
      flex-wrap: wrap;
    }

    .moveBar {
      padding-inline: 5px;
      width: 100%;
      height: 50px;
      background-color: rgb(53, 53, 53);

      input {
        height: 30px;
        margin: 5px 0;

        border: none;
        border-radius: 5px;

        background-color: #fff;
        padding: 5px;

        margin-bottom: 10px;
      }
    }

    .event {
      padding: 10px;
      width: 400px;
      height: fit-content;
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);

      &.active {
        border: 1px solid blue;
      }

      .eventTitle {
        width: 90%;
        height: 30px;
        margin: 5px 0;

        border: none;
        border-radius: 5px;

        background-color: #fff;
        padding: 5px;

        margin-bottom: 10px;
      }

      .eventItems {
        display: flex;
        flex-direction: column;
        gap: 20px;
      }
    }
  }

  .json {
    width: 50%;
  }
}
