.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 300px;
  height: 200px;

  padding: 20px;

  border-radius: 0 50px 0 0;

  background-color: #1191fa;

  input {
    width: 90%;
    height: 30px;
    margin: 5px 0;

    border: none;
    border-radius: 5px;

    background-color: #fff;
    padding: 5px;

    margin-bottom: 10px;
  }
}
