.container {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 300px;
  height: 200px;

  padding: 5px;

  border-radius: 0 50px 0 0;

  background-color: #1191fa;

  textarea {
    width: 90%;
    height: 90%;

    border: none;
    border-radius: 0 50px 0 0;

    background-color: transparent;

    color: white;
    font-size: 1rem;
    font-weight: 600;
    padding: 10px;

    resize: none;
  }
}
