.container {
  display: flex;
  flex-direction: column;

  padding: 10px;

  width: 300px;
  height: fit-content;
  background: #c2e4ff;
  border-radius: 0px 20px 20px 20px;

  input {
    height: 30px;
    margin: 5px 0;

    border: none;
    border-radius: 5px;

    background-color: #fff;
    padding: 5px;

    margin-bottom: 10px;
  }

  .add {
    display: flex;
    justify-content: center;
    width: 25px;
    height: 25px;
    cursor: pointer;
    background-color: red;

    margin-bottom: 10px;

    // add plus sign
    &:after {
      content: "+";
      color: #fff;
      font-size: 20px;
      font-weight: bold;
    }
  }

  .check {
    display: flex;
    gap: 10px;
    justify-content: flex-start;
    align-items: center;

    .checkbox {
      width: 20px;
      height: 20px;
    }
  }
}
