.container {
  display: flex;
  flex-direction: column;

  padding: 10px;

  width: 300px;
  height: 200px;
  background: #c2e4ff;
  border-radius: 0px 20px 20px 20px;

  input {
    width: 90%;
    height: 30px;
    margin: 5px 0;

    border: none;
    border-radius: 5px;

    background-color: #fff;
    padding: 5px;
  }

  button {
    width: 90%;
    height: 30px;
    margin: 5px 0;

    border: none;
    border-radius: 5px;

    background-color: #1191fa;
    color: #fff;
    font-weight: 600;
    font-size: 1rem;
    padding: 5px;

    cursor: pointer;

    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
  }
}
