.container {
  display: flex;
  flex-direction: column;

  padding: 10px;

  width: 300px;
  height: fit-content;
  background: #c2e4ff;
  border-radius: 0px 20px 20px 20px;

  input {
    width: 90%;
    height: 30px;
    margin: 5px 0;

    border: none;
    border-radius: 5px;

    background-color: #fff;
    padding: 5px;

    margin-bottom: 10px;
  }

  iframe {
    border: none;
    border-radius: 5px;
  }
}
